.form {
  $this: &;
  &__btn {
    font-size: 0; }
  &--consultation {
    .form {
      &__row {
        display: flex;
        margin-bottom: 40px;
        @include lg {
          display: block; } }
      &__field {
        @include lg {
          &:not(:last-child) {
            margin-bottom: 10px; } }
        &:not(:last-child) {
          margin-right: 60px; } } } }
  &--popup {
    #{$this}__field {
      &:not(:last-child) {
        margin-bottom: 20px; }
      &:last-child {
        margin-bottom: 40px; } } } }

.input {
  @include light;
  input {
    height: 32px;
    border-bottom: 1px solid  #202020;
    background-color: transparent;
    padding-top: 5px;
    padding-bottom: 5px;
    min-width: 282px;
    font-family: inherit;
    @include placeholder {
      color: #979797;
      font-size: 15px;
      font-family: inherit;
      @include light; } }

  &--full {
    input {
      width: 100%;
      min-width: 0; } }
  &--gray {
    input {
      border-bottom-color: $c-border; } }
  &--dynamic {
    position: relative;
    input {
      padding-left: 20px; }
    .input__icon {
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      left: 0;
      .icon {
        fill: #979797;
        font-size: 14px; } }
    &::before {
      content: '';
      display: inline-block;
      @include icon-arrow(10, 10, 1, #979797, -45);
      transform: translate(0, -50%) rotate(-45deg);
      position: absolute;
      top: 50%;
      right: 0; } } }

